import styled from 'styled-components';
import {MdKeyboardArrowRight ,MdArrowForward } from 'react-icons/md';

import {Link as LinkR } from 'react-router-dom'



export const HeroContainer = styled.div`
 background: #0c0c0c;
 display: flex;
 justify-content: center;
 align-items: center;
 padding: 0 0px;
 height:600px;
 position : relative;
 z-index: 1;

 :before{
     content:'';
     top: 0;
     left: 0;
     right: 0;
     bottom:0;
     background: linear-gradient(180deg , rgba(0,0,0,0.2) 0% ,
     rgba(0,0,0,0.6) 100%) ,linear-gradient(180deg,rgba(0,0,0,0.2) 0% , transparent 100% );
     z-index:2;
     }

`


export const HeroBg = styled.div`
 position:absolute;
 margin-top: 0px;
 top: 0;
 right:0;
 bottom: 0;
 left:0;
 width: 100%;
 height: 100%;
 overflow:hidden;

`


export const VideoBg = styled.video`
 width: 100%;
 height: 100%;
 -o-object-fit: cover;
 object-fit : cover;
 background: #232a34;


`
export const ImgBg = styled.img`
width: 100%;
 height: 100%;
 -o-object-fit: cover;
 object-fit : cover;
 opacity: 90%;

`


export const HeroContent = styled.div`
 z-index : 3;
 max-width: 1200px;
 position: absolute;
 padding : 8px 24px;
 display: flex;
 flex-direction: column;
 align-items: center;

`

export const HeroH1 = styled.h1`
 border:50px;
 border-radius:17px;
 color: #fff;
 font-size: 48px;
 text-align: center;
 background: #002D62;
 padding: 10px 22px;
 background-color:rgba(0, 45, 98,0.85);

 @media screen and (max-width: 768px)
 {
     font-size:40px;
 }

 @media screen and (max-width: 480px)
 {
     font-size:32px;
 }

`

export const HeroP = styled.p`
 margin-top: 24px;
 color:#fff;
 text-align: center;
 max-width : 600px;

 @media screen and (max-width: 768px)
 {
     font-size:24px;
 }

 @media screen and (max-width: 480px)
 {
     font-size:18px;
 }


`

export const HeroBtnWrapper = styled.div`
 margin-top : 32px;
 display : flex;
 flex-direction : column;
 align-items: center;

`

export const  ArrowForward = styled(MdArrowForward)`
 margin-left: 8px;
 padding-top:5px;
 font-size: 20px;

`


export const  ArrowRight = styled(MdKeyboardArrowRight)`
 font-size: 20px;

`


export const HeroBtnLink = styled(LinkR)`
 border-radius: 50px;
 background: #002D62;
 white-space: nowrap;
 padding: 10px 22px;
 color: #010686;
 font-size: 20px;
 outline: none;
 border:none;
 cursor: pointer;
 transition: all 0.2s ease-in-out;
 text-decoration: none;
 color:#F0F8FF;
 &:hover {
     transition: all 0.2s ease-in-out;
     background: #fff;
     color: #010606;
 }
`